.experience-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}
.experience-container > div{
    background: var(--color-bg-varient);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
}
.experience-container > div:hover{
    background: transparent;
    border-color: var(--color-primary-varient);
    cursor: default;
}
.experience-container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.experience-content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}
.experience-detail{
    display: flex;
    gap: 1rem;
}
.experience-detail-icon{
    margin-top: 6px;
    color: var(--color-primary);
}

/* ----------------------------------------media query tablet */
@media screen and (max-width:1024px){
   .experience-container{
    grid-template-columns: 1fr;
   }
   .experience-container > div{
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
   }
   .experience-content{
    padding: 1rem;
   }
 }

  /* ----------------------------------------media query mobile */
  @media screen and (max-width:600px){
    .experience-container{
        gap: 1rem;
    }
    .experience-container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
 
  }